import logo from './logo.svg';
import './App.css';
import React, { useState, useCallback } from 'react';
import ReactFlow, { ReactFlowProvider, addEdge, Background } from 'react-flow-renderer';
import CustomNode from './CustomNode';
import { Grid, Button, TextField, AppBar, Toolbar, Typography } from '@material-ui/core';
import SearchIcon from '@material-ui/icons/Search';
import InputBase from '@material-ui/core/InputBase';
import { fade, makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles((theme) => ({

  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  search: {
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    '&:hover': {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(1),
      width: 'auto',
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)}px)`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      width: '12ch',
      '&:focus': {
        width: '20ch',
      },
    },
  },
}));
function App() {

  const nodeTypes = {
    selectorNode: CustomNode,
  };
  const initialElements = []
  const classes = useStyles();
  const [rfInstance, setRfInstance] = useState(null);
  const [elements, setElements] = useState(initialElements);
  const [nodeName, setNodeName] = useState('');
  const text = 'Requirement Tracability Matrix'
  const height = (window.innerHeight - 40) + 'px'
  const graphStyles = { width: "100%", height: height, margin: '10px' };
  const onConnect = (params) => setElements((els) => addEdge(params, els));
  const onLoad = useCallback((instance) => {
    instance.fitView();
  }, []);
  const handleNodeChange = (e) => {
    //  setNodeName(e.target.value);
  }
  const getNodeId = () => `randomnode_${+new Date().getTime()}`;
  const onAdd = useCallback(() => {
    const newNode = {
      id: getNodeId(),
      type: 'selectorNode',
      style: { width: 'auto', maxWidth: '200px', height: '50px', fontSize: '8px', background: '#ffffff' },
      data: { name: nodeName, description: 'React Flow is a library for building node-based applications. These can be simple static diagrams or complex node-based editors.' },
      position: {
        x: 0,
        y: 20,
      },
    };
    setNodeName('');
    setElements((els) => els.concat(newNode));
  }, [setElements, nodeName, setNodeName, handleNodeChange, getNodeId]);
  const handleNameChange = (e) => {
    setNodeName(e.target.value);
  }

  const BasicGraph = () => <ReactFlowProvider><ReactFlow className="workpace" elements={elements} style={graphStyles}
    snapToGrid={true} onConnect={onConnect} nodeTypes={nodeTypes}
    defaultZoom={1.5} >  <Background color="#aaa" gap={16} /> </ReactFlow></ReactFlowProvider>;

  return (
    <div className="App">
      <header className="App-header">


        <Grid container spacing={3}>

          <Grid item xs={12}>
            <AppBar position="static">

              <Toolbar> <Typography variant="h6" color="inherit">
                {text}
              </Typography>

                <div className={classes.search}>
                  <div className={classes.searchIcon}>
                    <SearchIcon />
                  </div>
                  <InputBase
                    placeholder="Searchâ€¦"
                    classes={{
                      root: classes.inputRoot,
                      input: classes.inputInput,
                    }}
                    inputProps={{ 'aria-label': 'search' }}
                  />
                </div>
              </Toolbar>
            </AppBar>
          </Grid>
          <Grid item xs={10}>
            <BasicGraph />
          </Grid>
          <Grid item xs={2}>
            <Grid container spacing={3}>
              <Grid item xs={12}> <TextField id="outlined-basic" value={nodeName} onChange={handleNameChange} label="Node Name" variant="outlined" /></Grid>
              <Grid item xs={12}><Button onClick={onAdd} variant="contained">Add Node</Button></Grid>
            </Grid>
          </Grid>
        </Grid>
      </header>
    </div>
  );
}

export default App;
